            if (!(resp && resp.data.code === '200')) {
              this.$message({
                type: 'fail',
                message: resp.data.msg
              });
              return;
            }
            let result = resp.data.msg;

            for (let i = 0; i < result.length; i++) {
              this.transactionList.push({
                code: result[i].code,
                money: result[i].money,
                date: result[i].date,
                payMan: result[i].payMan,
                type: result[i].hospitalName
              });
            }
          });
        });
      },

      showChangeDialog(row) {
        console.log(row)
        this.changeDialog = true;
        this.changeDTO.orgName = row.orgName;
        this.changeDTO.orgId = row.orgId;
        this.changeDTO.changeOrgName = row.orgName;
        this.changeDTO.changeOrgId = row.orgId;
        this.changeDTO.orderId = row.orderId;
        this.changeDTO.orderCode = row.orderCode;
        this.changeDTO.hospitalizedDate = row.hospitalizedDate;
        this.changeDTO.patientCode = row.patientCode;
        this.changeDTO.patientName = row.patientName;
        this.changeDTO.serviceType = row.serviceType;
        this.changeDTO.price = row.price;
        this.changeDTO.serviceTypePro = row.serviceTypePro;
        this.changeDTO.serviceTypeId = row.careTypeId;
        this.changeDTO.serviceCode = row.serviceCode;
        this.changeDTO.startDate = row.startDate;
        this.changeDTO.settleType = row.settleType;
        this.changeDTO.bedNum = row.bedNum;



      },
      showOrgTableChange() {
        this.changeOrgTree = true;
      },

      orgClickChange() {
        let data = this.$refs.treeChange.getCheckedNodes();
        this.changeDTO.changeOrgId = data[0].id;
        this.changeDTO.changeOrgName = data[0].label;
        this.changeOrgTree = false;
      },
      orgNodeClickChange(data, nodes) {
        let target = [];
        target.push(data);
        this.$refs.treeChange.setCheckedNodes(target, true)
      },

      //陪护类型参照界面
      showService() {
        this.careTypeSearch = true;
        this.getServiceTypeload();
      },
      //分页操作
      currentChange(currentChange) {
        this.careTypeSearchDialogCurrentPage = currentChange;
        this.getServiceTypeload();
      },
      //查询陪护类型列表
      getServiceTypeload() {
        this.postRequestJson('/busi/baseinfo/caretype/list', {
          curPage: this.careTypeSearchDialogCurrentPage,
          projectId: 2
        }).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.CareTypeList = resp.data.msg.result;
            this.careTypeSearchDialogTotalCount = resp.data.msg.totalSize;
          } else {
            this.$confirm(resp.data.msg, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
            }).catch(() => {
            });
          }
        })
      },
      //陪护类型参照选中项
      SelectionChange(val){
        this.resourceSelection = val;
      },
      //陪护类型参照确定按钮
      ServiceClick() {
        this.changeDTO.changeServiceTypeName = this.resourceSelection.name;
        this.changeDTO.changeServicePrice = this.resourceSelection.price;
        this.changeDTO.changeServiceTypeId = this.resourceSelection.id;
        this.changeDTO.changeSettleType = this.resourceSelection.settleTypeName;

        this.careTypeSearch = false;
      },
      //变更服务确定按钮
      submitChange(){

        let id = this.changeDTO.orderId;
        let requestCode = this.changeDTO.serviceCode;
        let bedNum = this.changeDTO.changeBedNum;
        let careTypeId = this.changeDTO.changeServiceTypeId;
        let orgId = this.changeDTO.changeOrgId;
        let changeDate = this.changeDTO.changeDate;
        let lastServiceEndDate = this.changeDTO.lastServiceEndDate;
       // let startDate = this.changeDTO.startDate;

        this.putRequestJson('/busi/patient',{
          id:id,
          requestCode:requestCode,
          bedNum:bedNum,
          careTypeId: careTypeId,
          orgId:orgId,
          changeDate: changeDate,
          //startDate: startDate,
          lastServiceEndDate: lastServiceEndDate,
          clerk:'admin'
        }).then(resp => {
          if (!(resp && resp.data.code === '200')) {
            this.getMasgg(resp.data.msg);
            return;
          }
          this.$message({
            type: 'fail',
            message: resp.data.msg
          });
        });

      },

      //服务单据数据
      SelectionPatinChange(val){
        this.resourcePatin = val;
      },
      SelectionTransactionChange(val){
        this.resourceTransaction = val;
      },
      //打印预收单界面
      showPatinView() {
        this.dialogTitle = "打印预收单";
        this.getSetPatin();
      },
      //打印收据单界面
      showSJView(){
        this.dialogTitle = "打印收据";
        this.getSetSj();
      },
       //打印续费单界面
      renewView(){
        this.dialogTitle = "打印续费收据";
        this.renewDialogVisible = true;
        this.getRenew();
      },
      //打印预收单设置参数
      getSetPatin(){

        if(this.detailDTO.totalServiceAmount<0){
           this.getMasgg("预收金额要大于零");
           return;
        }
        if(this.resourcePatin.orgName===undefined){
          this.getMasgg("请选最新一条服务记录，进行打印");
          this.resourcePatin.orgName=undefined;
          return;
        }
        this.dialogVisible = true;
        var myDate = new Date();
        this.printing.y_m_d= myDate.getFullYear()+"年"+(myDate.getMonth()+1)+"月"+myDate.getDate()+"日";
        this.getRequest("/busi/baseinfo/docfile?id=1").then(resp=> {
          if (resp && resp.status === 200) {
            this.printing.dy_code=resp.data.msg.currentCode;//单据编号
          }else{
            this.getMasgg(resp.data.msg);
          }
        });
        this.printing.br_code= this.detailDTO.orderCode;
        this.printing.dy_ks= this.resourcePatin.orgName;//科室
        this.printing.dy_ch=this.resourcePatin.bedNum;  //床号
        this.printing.dy_dh=this.detailDTO.phone;//手机号
        this.printing.dy_xm=this.detailDTO.patientName;//姓名
        this.printing.dy_sfzh="";//身份证
        this.printing.dy_lx=this.resourcePatin.serviceTypeName;//陪护类型
        this.printing.dy_dj=this.resourcePatin.price;//单价
        this.printing.dy_ksr=this.resourcePatin.startDate.substring(0, 10);//开始时间
        this.printing.dy_ze=this.resourceTransaction.money;//预收款
        this.printing.dy_jsr=this.user.name;//经手人
        //转换大写金额----------
        //截取数字.去掉小数
        var  strmoney= this.padding(this.resourceTransaction.money,5);
        let w =strmoney.charAt(strmoney.length-5);//万
        let q =strmoney.charAt(strmoney.length-4);//千
        let b =strmoney.charAt(strmoney.length-3);//百
        let s =strmoney.charAt(strmoney.length-2);//十
        let g =strmoney.charAt(strmoney.length-1);//个
        for(let i=0;i<1;i++)
        {
          this.printing.w =this.convertCurrency(w)!==""?this.convertCurrency(w):"零";
          this.printing.q =this.convertCurrency(q)!==""?this.convertCurrency(q):"零";
          this.printing.b =this.convertCurrency(b)!==""?this.convertCurrency(b):"零";
          this.printing.s=this.convertCurrency(s)!==""?this.convertCurrency(s):"零";
          this.printing.g =this.convertCurrency(g)!==""?this.convertCurrency(g):"零";
        }
      },
      //打印收据设置参数
      getSetSj(){
       var myDate = new Date();
       if(this.detailDTO.leaveDate===null){
         this.getMasgg("该病人还没出院，不能打印收据");
          return;
       }
        if(this.resourcePatin.orgName===undefined){
          this.getMasgg("请选最新一条服务记录，进行打印");
          this.resourcePatin.orgName=undefined;
          return;
        }
        this.sJDialogVisible = true;
       this.printingSj.p_date_y=myDate.getFullYear();
       this.printingSj.p_date_m=myDate.getMonth() + 1;
       this.printingSj.p_date_d=myDate.getDate();
       this.getRequest("/busi/baseinfo/docfile?id=2").then(resp=> {
         if (resp && resp.status === 200) {
           this.printingSj.br_code="NO"+resp.data.msg.currentCode; //单据编号
         }else{
           this.getMasgg(resp.data.msg);
         }
       });
       this.printingSj.p_br_code=this.detailDTO.orderCode; //住院编号
       this.printingSj.p_dy_ks= this.resourcePatin.orgName; //科室
       this.printingSj.p_dy_ch= this.resourcePatin.bedNum;  //床号
       this.printingSj.p_dy_xm=this.detailDTO.patientName; //姓名
       this.printingSj.p_jsr=this.user.name;  //经手人
       //this.printingSj.p_dy_q=this.detailDTO.totalServiceAmount + 2;  //服务金额
        var finalPrintMoney=this.detailDTO.totalServiceAmount + this.detailDTO.refund
        this.printingSj.p_dy_q=finalPrintMoney;  //服务金额+退款金额,zack@20190105,退款金额为负数

       this.printingSj.p_dy_fwsj1=this.detailDTO.hospitalizedDate.substring(0, 10);  //服务开始时间
       this.printingSj.p_dy_fwsj2=this.detailDTO.leaveDate.substring(0, 10);  //服务结束时间
       //转换大写金额-----
       //截取数字.去掉小数
       //var  strmoney= this.padding(this.detailDTO.totalServiceAmount + 2,4);
        var  strmoney= this.padding(Math.abs(finalPrintMoney),4);//zack@20190105,退款金额打印大写

        let q =strmoney.charAt(strmoney.length-4);//千
       let b =strmoney.charAt(strmoney.length-3);//百
       let s =strmoney.charAt(strmoney.length-2);//十
       let g =strmoney.charAt(strmoney.length-1);//个
       for(let i=0;i<1;i++)
       {
         this.printingSj.p_q=this.convertCurrency(q)!==""?this.convertCurrency(q):"零";
         this.printingSj.p_b=this.convertCurrency(b)!==""?this.convertCurrency(b):"零";
         this.printingSj.p_s=this.convertCurrency(s)!==""?this.convertCurrency(s):"零";
         this.printingSj.p_g =this.convertCurrency(g)!==""?this.convertCurrency(g):"零";
       }
     },
      //打印续费单设置参数
      getRenew(){
        var myDate = new Date();
        this.renew.xf_ymd= myDate.getFullYear()+"年"+(myDate.getMonth()+1)+"月"+myDate.getDate()+"日";
        this.getRequest("/busi/baseinfo/docfile?id=1").then(resp=> {
          if (resp && resp.status === 200) {
            this.renew.xf_Rcode=resp.data.msg.currentCode;//单据编号
          }else{
            this.getMasgg(resp.data.msg);
          }
        });


        this.renew.xf_code= this.curRow.orderCode;
        this.renew.xf_ks= this.curRow.orgName;//科室
        this.renew.xf_ch=this.resourcePatin.bedNum;  //床号
        this.renew.xf_dh=this.chargeDTO.phone;//手机号
        this.renew.xf_xm=this.chargeDTO.patientName;//姓名
        this.renew.xf_sfzh="";//身份证
        this.renew.xf_lx=this.curRow.serviceType;//陪护类型
        this.renew.xf_dj=this.curRow.price;//单价
        this.renew.xf_ksr=this.chargeDTO.date.substring(0, 10);//开始时间
        this.renew.xf_ze=this.chargeDTO.money;//预收款
        this.renew.xf_jsr=this.user.name;//经手人
        //转换大写金额----------
        //截取数字.去掉小数
        var  strmoney= this.padding(this.chargeDTO.money,5);
        let w =strmoney.charAt(strmoney.length-5);//万
        let q =strmoney.charAt(strmoney.length-4);//千
        let b =strmoney.charAt(strmoney.length-3);//百
        let s =strmoney.charAt(strmoney.length-2);//十
        let g =strmoney.charAt(strmoney.length-1);//个
        for(let i=0;i<1;i++)
        {
          this.renew.xf_w =this.convertCurrency(w)!==""?this.convertCurrency(w):"零";
          this.renew.xf_q =this.convertCurrency(q)!==""?this.convertCurrency(q):"零";
          this.renew.xf_b =this.convertCurrency(b)!==""?this.convertCurrency(b):"零";
          this.renew.xf_s=this.convertCurrency(s)!==""?this.convertCurrency(s):"零";
          this.renew.xf_g =this.convertCurrency(g)!==""?this.convertCurrency(g):"零";
        }
      },
      //金额转换
      convertCurrency(money){
        //汉字的数字
        var cnNums = new Array('零', '壹', '贰', '叁', '肆', '伍', '陆', '柒', '捌', '玖');
        //基本单位
        var cnIntRadice = new Array('', '拾', '佰', '仟');
        //对应整数部分扩展单位
        var cnIntUnits = new Array('', '万', '亿', '兆');
        //对应小数部分单位
        var cnDecUnits = new Array('角', '分', '毫', '厘');
        //整数金额时后面跟的字符
        var cnInteger = '';
        //整型完以后的单位
        var cnIntLast = '';
        //最大处理的数字
        var maxNum = 999999999999999.9999;
        //金额整数部分
        var integerNum;
        //金额小数部分
        var decimalNum;
        //输出的中文金额字符串
        var chineseStr = '';
        //分离金额后用的数组，预定义
        var parts;
        if (money == '') { return ''; }
        money = parseFloat(money);
        if (money >= maxNum) {
          //超出最大处理数字
          return '';
        }
        if (money == 0) {
          chineseStr = cnNums[0] + cnIntLast + cnInteger;
          return chineseStr;
        }
        //转换为字符串
        money = money.toString();
        if (money.indexOf('.') == -1) {
          integerNum = money;
          decimalNum = '';
        } else {
          parts = money.split('.');
          integerNum = parts[0];
          decimalNum = parts[1].substr(0, 4);
        }
        //获取整型部分转换
        if (parseInt(integerNum, 10) > 0) {
          var zeroCount = 0;
          var IntLen = integerNum.length;
          for (var i = 0; i < IntLen; i++) {
            var n = integerNum.substr(i, 1);
            var p = IntLen - i - 1;
            var q = p / 4;
            var m = p % 4;
            if (n == '0') {
              zeroCount++;
            } else {
              if (zeroCount > 0) {
                chineseStr += cnNums[0];
              }
              //归零
              zeroCount = 0;
              chineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
            }
            if (m == 0 && zeroCount < 4) {
              chineseStr += cnIntUnits[q];
            }
          }
          chineseStr += cnIntLast;
        }
        //小数部分
        if (decimalNum != '') {
          var decLen = decimalNum.length;
          for (var i = 0; i < decLen; i++) {
            var n = decimalNum.substr(i, 1);
            if (n != '0') {
              chineseStr += cnNums[Number(n)] + cnDecUnits[i];
            }
          }
        }
        if (chineseStr == '') {
          chineseStr += cnNums[0] + cnIntLast + cnInteger;
        } else if (decimalNum == '') {
          chineseStr += cnInteger;
        }
        return chineseStr;
      },
      //位数不够在前补零
      padding(num, length) {
        for(var len = (num + "").length; len < length; len = num.length) {
          num = "0" + num;
        }
        return num;
      },
      //打印
      divPrint(id){
        let subOutputRankPrint = document.getElementById(id);
        console.log(subOutputRankPrint.innerHTML);
        let newContent =subOutputRankPrint.innerHTML;
        let oldContent = document.body.innerHTML;
        document.body.innerHTML = newContent;
        window.print();
        window.location.reload();
        document.body.innerHTML = oldContent;
        return false;
      },
      //消息提示
      getMasgg(data){
        this.$confirm(data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.changeDialog=false;
          this.searchPatientList();

        }).catch(() => {
        });
      },
    },
    computed: {
      user(){
        return this.$store.state.user;
      }
    }
  };
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
  .labelstyle1 {
    width:80px;
    display: inline-block;
    border-bottom: 1px solid #000;
    text-align: center
  }
  .labelstyle{
    width:80px;
    display:inline-block;
    border-bottom:1px solid #fff;
    text-align:center;
    margin-top: -35px;
  }
  .labelstyle2 {
    width: 71px;
    display: inline-block;
    border-bottom: 1px solid #000;
    text-align: center
  }
  table {
    border-collapse: collapse;
  }
  th, td {
    padding: 0.5em 1em;
    font-size: 14px;
  }

  .el-table--striped .el-table__body tr.el-table__row--striped.current-row td, .el-table__body tr.current-row>td, .el-table__body tr.hover-row.current-row>td, .el-table__body tr.hover-row.el-table__row--striped.current-row>td, .el-table__body tr.hover-row.el-table__row--striped>td, .el-table__body tr.hover-row>td {
    background-color: #409effa1;
  }

</style>



