                      <tr>
                        <td style="width:30%;"></td>
                        <td align="right" style="width:40%;">
	                      <span style="position:relative;left:53px;top:-10px;">
	                         NO: <label id="dy_code" style="width:100px;">{{printing.dy_code}}</label>
	                      </span>
                        </td>
                        <td style="width:30%;">
                          <label id="y_m_d">{{printing.y_m_d}}</label>
