                        <td colspan="3">
                          <span>&nbsp; &nbsp; &nbsp; 今收到
                            <label id="dy_ks" class="labelstyle">{{printing.dy_ks}}</label>科
                            <label id="dy_ch" class="labelstyle" style="width:65px;">{{printing.dy_ch}}</label>床
                            <label id="dy_xm" class="labelstyle" style="width:65px;">{{printing.dy_xm}}</label>产妇/患者
                            <label id="dy_sfzh" class="labelstyle" style="width:150px;">{{printing.dy_sfzh}}</label>身份证
                          </span>
                        </td>
                      </tr>
                      <tr>
                        <td colspan="3">
				                  <span>手机号码<label id="dy_dh" class="labelstyle" style="width:107px;">{{printing.dy_dh}}</label>
                                 交来预收款<label id="dy_lx" class="labelstyle"
                                             style="width:80px;">{{printing.dy_lx}}</label>
