                      <tr style="margin-top:5px;margin-bottom:5px">
                        <td colspan="3">
                          合计大写金额：
                          <label id="w" class="labelstyle" style="width:70px;">{{printing.w}}</label>万
                          <label id="q" class="labelstyle" style="width:70px;">{{printing.q}}</label>仟
                          <label id="b" class="labelstyle" style="width:70px;">{{printing.b}}</label>佰
                          <label id="s" class="labelstyle" style="width:70px;">{{printing.s}}</label>拾
                          <label id="g" class="labelstyle" style="width:70px;">{{printing.g}}</label>元整
                        </td>
                      </tr>
                      <tr style="margin-top:5px;margin-bottom:5px">
                        <td colspan="3">
                          <span>收款单位  （盖章） </span>
                        </td>
                      </tr>
                    </table>

                  </div>
                  <div align="right" style="width:602.5px;height:25px;margin:0 auto;font-size:12px;">
                    <label style="position:relative;top:5px;left:-10px">经手人：</label>
                    <label id="dy_jsr"
                           style="width:70px;position:relative;top:5px;left:-10px">{{printing.dy_jsr}}</label>
                  </div>
                </div>
                <div style="float:left;height:260px;width:16px;font-size:11px;text-align:right;">
                  <a style="width:5px;word-wrap:break-word;word-spacing:3px;position:relative;top:30px;">①存根︵ 白︶</a>
                  <a style="width:5px;word-wrap:break-word;word-spacing:3px;position:relative;top:40px;">②收据︵红︶</a>
                </div>
              </div>
