          <el-row>
            <el-button type="primary" size="mini" icon="el-icon-printer" @click.prevent="divPrint">点击打印</el-button>
          </el-row>
        </el-dialog>
      </div>

    </el-form>


    <!-- 科室对照界面-->
    <el-form ref="OrgForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog :title="orgdialogTitle" style="padding: 0px;" :close-on-click-modal="false"
                   :visible.sync="EdialogVisible" width="30%">
          <el-row>
            <el-tree
              :props="defaultProps"
              :load="loadChild"
              node-key="id"
              lazy
              show-checkbox
              ref="tree"
              @check="orgNodeClick"
            >
            </el-tree>
