                align="center"
                label="价格">
              </el-table-column>
              <el-table-column
                prop="settleTypeName"
                fixed
                align="center"
                label="计算方式">
              </el-table-column>
              <el-table-column
                prop="salaryIndex"
                fixed
                align="center"
                label="工资系数">
              </el-table-column>
